@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  *{
    @apply font-serif !important;
    @apply bg-transparent !important;
  }
  html {
    @apply bg-tegami-100 !important;
  }
  body {
    @apply text-tegami-900 !important;
    /* @apply antialiased; */
    @apply items-center;
    @apply justify-center;
  } 
  p, h1, h2, h3, h4, h5, h6, div {
    @apply text-tegami-900 !important;
    @apply text-lg !important;
  }
 newsletter:p
  a {
    @apply text-tegami-500 !important;
  }

  strong {
    @apply font-bold !important;
;
  }

}

@layer utilities {
  .newsletter-text {
    @apply text-2xl !important;
  }
}